import tagSexAge from '@/components/common/tag-sex-age.vue'
export default {
	props: {
		userInfo:Object
	},
	data () {
		return {
			bgImg: this.userInfo.bgImg
		}
	},
	components: {
		tagSexAge
	},
	computed: {
		getBgImg() {
			return `../../static/bgimg/${this.bgImg}.jpg`
		}
	},
	methods: {
		// 切换背景
		changeBgImg () {
			let no = parseInt(this.bgImg)
			this.bgImg = no < 4 ? ++no : 1
		},
		follow() {
			this.$emit('changeFollow')
		}
	},
}
